import { Component, OnInit } from '@angular/core';
import { LoadingController, ModalController } from '@ionic/angular';
import { UntypedFormBuilder, UntypedFormGroup, Validators } from '@angular/forms';


import { ToastService } from 'src/app/services/toast/toast.service';

import { AuthService } from 'src/app/services/auth/auth.service';
import { ActivatedRoute, ParamMap } from '@angular/router';
import { Router } from '@angular/router';

@Component({
  selector: 'app-trade',
  templateUrl: './trade.page.html',
  styleUrls: ['./trade.page.scss'],
})
export class TradePage implements OnInit {
  frm_book_trade: UntypedFormGroup;
  submit_attempt: boolean = false;

  coin_1: any;
  coin_2: any;

  constructor(

    private r: ActivatedRoute,
    private formBuilder: UntypedFormBuilder,
    private authService: AuthService,
    private toastService: ToastService
  ) { }


  ngOnInit() {

    const coin_1 = this.r.snapshot.paramMap.get('coin');


    this.coin_1 = coin_1;
    this.coin_2 = "USDT";





    // Setup form
    this.frm_book_trade = this.formBuilder.group({
      price: ['', Validators.required],
      amount: ['1', Validators.required],
      coin_1: [this.coin_1, Validators.required],
      coin_2: [this.coin_2, Validators.required],
    });



    this.authService
      .exchange_rate(this.frm_book_trade.value)
      .subscribe((data) => {

        this.frm_book_trade.get('price').setValue(data['amount']);



      });

  }



  submit() {

    this.submit_attempt = true;




    this.authService
      .book_trade(this.frm_book_trade.value)
      .subscribe((data) => {


        this.toastService.presentToast('Success', 'Trade Submitted', 'top', 'success', 4000);


      });





  }
  ngOnDestroy() {

    //this.r.unsubscribe();
  }
  getexchangerate(e: any) {


    console.log(this.frm_book_trade.value);



    this.authService
      .exchange_rate(this.frm_book_trade.value)
      .subscribe((data) => {

        

        this.frm_book_trade.get('price').setValue(data['amount']);

      });

  }
 
}
